* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #aae1f2;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px 10px 10px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

#search-box {
  font: inherit;
  max-width: 320px;
  background-color: #fff;
  border: none;
  border-radius: 25px;
  margin-bottom: 30px;
  padding: 11px;
  transition: all .3s;
}

#search-box:focus {
  background-color: #daeff4;
  border-color: #a6c8d1;
}

.country-flag {
  width: 50px;
  height: auto;
  outline: .5px solid #000;
  transition: all .3s;
}

.one-card {
  min-width: 450px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: .25rem;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 5px;
  padding: 15px 25px;
  transition: all .3s;
  display: inline-flex;
  box-shadow: 10px 10px 19px 4px rgba(0, 0, 0, .69);
}

.one-card:hover {
  transform: translateY(-5px);
  box-shadow: 15px 15px 25px 4px rgba(0, 0, 0, .69);
}

.one-card div {
  justify-content: center;
  align-items: center;
  display: flex;
}

h2 {
  margin-right: 10px;
}

.info-part span {
  font-size: 19px;
}

.country-list {
  background-color: #fff;
  flex-direction: column;
  gap: 15px;
  list-style: none;
  display: flex;
  box-shadow: 10px 10px 19px 4px rgba(0, 0, 0, .69);
}

.country-list li {
  min-width: 400px;
  border-bottom: 1px solid #ccc;
  justify-content: start;
  align-items: center;
  padding: 17px 30px 25px;
  transition: all .3s;
  display: flex;
}

.country-list li:hover {
  background-color: #aae1f2;
  transform: translateX(10px);
  box-shadow: 10px 10px 19px 4px rgba(0, 0, 0, .69);
}

li p {
  margin-left: 10px;
  font-size: 18px;
}

label {
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header {
  text-align: center;
  color: #212121;
  text-transform: uppercase;
  letter-spacing: .2rem;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, .3);
  margin-bottom: 50px;
  font-size: 3rem;
  font-weight: bold;
  transition: color .5s;
}

.header:hover, #search-box:focus + .header {
  color: #d0eff5;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, .5);
  cursor: pointer;
}

